* {
  font-family: 'Roboto', Arial, sans-serif;
}

body {
  min-width: 980px;
  color: #333;
  background: #f8f8f8;
  font-family: 'Roboto', Arial, sans-serif;
  font-size: 14px;
  line-height: 1.3;
}

html,
body {
  height: 100%;
}

.out {
  display: flex;
  flex-direction: column;
  max-height: 100%;
  min-height: 600px;
}

.container {
  overflow: auto;
  flex-grow: 1;
  max-height: 100%;
}

.inner {
  padding: 20px;
  margin: 0 auto;
  max-width: 1024px;
  width: 100%;
}

[data-scrollbar]::-webkit-scrollbar {
  display: initial !important;
}

[data-scrollbar]::-webkit-scrollbar:vertical {
  width: 7px;
  background: linear-gradient(to right, transparent 3px, #5aaee2 3px, #5aaee2 4px, transparent 4px);
}

[data-scrollbar]::-webkit-scrollbar:horizontal {
  height: 7px;
  background: linear-gradient(to bottom, transparent 3px, #5aaee2 3px, #5aaee2 4px, transparent 4px);
}

[data-scrollbar]::-webkit-scrollbar-thumb {
  background: #2e99db;
}

.no-touch [data-scrollbar]::-webkit-scrollbar-thumb:hover {
  background: #1f7db7;
}

[data-animation*="ripple"] {
  position: relative;
  overflow: hidden;
}

[data-animation*="ripple"] .ripple-circle {
  position: absolute;
  z-index: 20;
  top: 50%;
  left: 50%;
  border-radius: 50%;
  width: 112px;
  height: 112px;
  background: rgba(0, 0, 0, 0.1);
  animation: ripple 0.5s linear forwards;
  pointer-events: none;
}

[data-animation*="white"] .ripple-circle {
  background: rgba(255, 255, 255, 0.1);
}

@keyframes ripple {
  0% {
    opacity: 1;
    transform: scale(0);
  }
  100% {
    opacity: 0;
    transform: scale(2);
  }
}

.heading {
  padding-bottom: 10px;
  margin-bottom: 15px;
  border-bottom: 1px solid rgba(51, 51, 51, 0.2);
  color: gray;
}

.description {
  color: #333;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.5;
}

.description:not(:last-child) {
  margin-bottom: 10px;
}

.label {
  display: inline-block;
  vertical-align: top;
  font-size: 16px;
  font-weight: 600;
  line-height: 16px;
}

.btn {
  position: relative;
  overflow: hidden;
  display: inline-block;
  vertical-align: top;
  padding: 0 15px;
  min-width: 120px;
  border: none;
  outline: none;
  color: #fff;
  background: #2e99db;
  font-size: 19px;
  font-weight: 300;
  line-height: 60px;
  text-align: center;
  text-decoration: none;
  cursor: pointer;
  transition: opacity .175s, background .175s, box-shadow .175s;
}

.btn:active {
  color: #fff;
  background: #1f7db7;
  box-shadow: inset 2px 2px 6px rgba(0, 0, 0, 0.2);
}

.btn:hover .icon-right {
  transform: translate3d(50%, 0, 0);
}

.btn_red {
  background: #ec4a41;
}

.btn_red:active {
  background: #e32117;
}

.btn .icon-right {
  display: inline-block;
  vertical-align: top;
  fill: currentColor;
  font-size: 80%;
  margin-top: 22px;
  transition: transform .175s;
}

.select {
  position: relative;
  user-select: none;
}

.select.is-active .select__arrow {
  transform: rotate(180deg);
}

.select.is-active .select__value {
  border-color: #2e99db;
}

.select__arrow {
  position: absolute;
  top: 50%;
  right: 15px;
  font-size: 8px;
  margin-top: -0.5em;
  pointer-events: none;
  transition: transform 0.25s;
}

.select__value {
  overflow: hidden;
  padding: 0 30px 0 15px;
  border: 1px solid #333;
  border-radius: 0;
  width: 100%;
  height: 60px;
  background: #fff;
  font-size: 16px;
  font-weight: 400;
  line-height: 58px;
  text-overflow: ellipsis;
  white-space: nowrap;
  cursor: pointer;
  transition: border-color 0.25s;
}

.select__dropdown {
  position: absolute;
  z-index: 200;
  top: 100%;
  left: 0;
  right: 0;
  overflow: auto;
  padding: 10px 0;
  margin-top: 10px;
  border: 1px solid #333;
  max-height: 200px;
  opacity: 0;
  visibility: hidden;
  background: #fff;
  transition: border-color 0.25s, opacity 0.3s, visibility 0.3s, transform 0.3s;
}

.select__dropdown[data-position="true"] {
  margin-top: -10px;
}

.select__dropdown.is-open {
  border-color: #2e99db;
  opacity: 1;
  visibility: visible;
}

.select__item {
  overflow: hidden;
  padding: 5px 15px;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.2;
  text-overflow: ellipsis;
  white-space: nowrap;
  cursor: pointer;
  transition: color 0.25s, background 0.25s;
}

.select__item:hover {
  color: #2e99db;
}

.select__item.is-active {
  color: #fff;
  background: #2e99db;
}

.input {
  padding: 0 15px;
  border: 1px solid #333;
  border-radius: 0;
  width: 100%;
  height: 60px;
  background: #fff;
  box-shadow: none;
  font-size: 16px;
  font-weight: 400;
  line-height: normal;
  appearance: none;
  transition: border-color 0.25s;
}

.input::-ms-clear {
  display: none;
}

.input:focus {
  border-color: #2e99db;
}

.input:disabled {
  border-color: #b3b3b3;
  background: #e6e6e6;
  cursor: not-allowed;
}

.radio {
  position: relative;
  display: inline-block;
  vertical-align: top;
}

.radio:after {
  content: " ";
  display: table;
  clear: both;
}

.radio__input {
  display: none;
}

.radio__input:checked ~ .radio__icon {
  border-color: #2e99db;
}

.radio__input:checked ~ .radio__icon:before {
  opacity: 1;
}

.radio__input:disabled ~ .radio__icon, .radio__input:disabled ~ .radio__label {
  opacity: 0.7;
  cursor: not-allowed;
}

.radio__icon {
  position: relative;
  float: left;
  border: 1px solid #333;
  border-radius: 50%;
  width: 20px;
  height: 20px;
  background: #fff;
  cursor: pointer;
  transition: border-color 0.25s;
}

.radio__icon:before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  margin: -0.5em 0 0 -0.5em;
  border-radius: 50%;
  width: 1em;
  height: 1em;
  opacity: 0;
  background: #2e99db;
  font-size: 10px;
  transition: opacity 0.25s;
}

.radio__label {
  display: block;
  overflow: hidden;
  padding-left: 10px;
  color: #333;
  font-size: 18px;
  font-weight: 600;
  line-height: 20px;
  cursor: pointer;
}

.form-group:not(:last-child) {
  margin-bottom: 20px;
}

.form-group_inline, .form-group_table {
  display: flex;
  align-items: center;
}

.form-group_inline .form-group__label, .form-group_table .form-group__label {
  padding-right: 15px;
}

.form-group_inline .form-group__field, .form-group_table .form-group__field {
  flex-grow: 1;
  min-width: 0;
}

.form-group_inlune .form-group__field {
  width: 100%;
}

.form-group_table .form-group__label {
  flex-grow: 1;
  width: 30%;
}

.form-group_table .form-group__field {
  width: 60%;
}

.form-group__label {
  padding: 10px 0;
}

.date-form {
  display: flex;
  align-items: center;
  margin: 0 -10px;
}

.date-form__col, .date-form__submit {
  padding: 0 10px;
}

.date-form__col {
  display: flex;
  align-items: center;
  flex-grow: 1;
  width: 50%;
  min-width: 0;
}

.date-form__label {
  padding-right: 15px;
  font-size: 18px;
}

.date-form__field {
  position: relative;
  flex-grow: 1;
  width: 100%;
  min-width: 0;
}

.datepicker {
  position: absolute;
  z-index: 200;
  top: 100%;
  left: 0;
  overflow: hidden;
  margin-top: 8px;
  border-radius: 4px;
  width: 320px;
  opacity: 0;
  visibility: hidden;
  background: #fff;
  box-shadow: 0 30px 40px 0 rgba(16, 36, 94, 0.2);
  transition: opacity 0.25s, visibility 0.25s;
}

.datepicker.is-open {
  opacity: 1;
  visibility: visible;
}

.datepicker__header {
  text-align: center;
}

.datepicker__header:after {
  content: " ";
  display: table;
  clear: both;
}

.datepicker__prev, .datepicker__next {
  width: 50px;
  height: 50px;
  font-size: 0;
  background: none;
  transition: background 0.25s;
}

.datepicker__prev:hover, .datepicker__next:hover {
  background: rgba(0, 0, 0, 0.05);
}

.datepicker__prev:active, .datepicker__next:active {
  background: rgba(0, 0, 0, 0.1);
}

.datepicker__prev .icon, .datepicker__next .icon {
  display: inline-block;
  vertical-align: middle;
  font-size: 14px;
}

.datepicker__prev {
  float: left;
  border-radius: 4px 0 0 0;
}

.datepicker__next {
  float: right;
  border-radius: 0 4px 0 0;
}

.datepicker__title {
  overflow: hidden;
  font-size: 16px;
  font-weight: 500;
  line-height: 50px;
}

.datepicker__body {
  padding: 0 10px 10px;
}

.datepicker__table {
  display: table;
  table-layout: fixed;
  width: 100%;
}

.datepicker__thead {
  display: table-header-group;
}

.datepicker__tbody {
  display: table-row-group;
}

.datepicker__tr {
  display: table-row;
}

.datepicker__th, .datepicker__td {
  display: table-cell;
  vertical-align: top;
  text-align: center;
}

.datepicker__th {
  padding: 5px 0;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.5;
  text-transform: capitalize;
}

.datepicker__td {
  font-size: 14px;
  font-weight: 400;
  line-height: 1.5;
  text-transform: uppercase;
}

.datepicker__day {
  display: block;
  padding: 5px 0;
  color: #333;
  text-decoration: none;
  transition: background 0.25s;
}

.datepicker__day:hover {
  background: rgba(0, 0, 0, 0.05);
}

.datepicker__day:active {
  background: rgba(0, 0, 0, 0.1);
}

.datepicker__day.is-weekend {
  color: red;
}

.datepicker__day.is-neighboring {
  color: #929292;
}

.datepicker__day.is-active {
  color: #fff;
  background: #2e99db;
}

[data-datepicker-input] {
  cursor: pointer;
}

.table {
  display: table;
  table-layout: fixed;
  min-width: 100%;
  font-size: 14px;
}

.table__thead {
  display: table-header-group;
}

.table__tbody {
  display: table-row-group;
}

.table__tr {
  display: table-row;
}

.table__th, .table__td {
  display: table-cell;
  vertical-align: top;
  padding: 10px;
  text-align: center;
}

.table__th {
  position: relative;
  color: #fff;
  background: #2e99db;
}

.table__th:not(:first-child):before {
  content: '';
  position: absolute;
  top: 8px;
  left: -1px;
  bottom: 8px;
  border-left: 2px solid rgba(0, 0, 0, 0.1);
}

.box {
  padding: 40px 30px;
  background: rgba(51, 51, 51, 0.07);
}

.box__type:not(:last-child) {
  margin-bottom: 20px;
}

.header {
  padding: 10px 40px;
  color: #fff;
  background: #2e99db;
  text-align: center;
}

.header__inner:after {
  content: " ";
  display: table;
  clear: both;
}

.header__logo {
  float: left;
  font-size: 0;
}

.header__logo img {
  height: 50px;
}

.header__logo ~ .header__title {
  padding-left: 130px;
  padding-right: 130px;
}

.header__title {
  padding: 10px 0;
  font-size: 28px;
  font-weight: 500;
  line-height: 30px;
  text-transform: uppercase;
}

.footer {
  padding: 20px 40px;
  border-top: 1px solid rgba(51, 51, 51, 0.2);
}

.footer__inner:after {
  content: " ";
  display: table;
  clear: both;
}

.footer__prev {
  float: left;
}

.footer__next {
  float: right;
}

.modal {
  position: fixed;
  z-index: 1000;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  opacity: 0;
  visibility: hidden;
  background: rgba(51, 51, 51, 0.9);
  transition: opacity 0.3s, visibility 0.3s;
}

.modal.is-open {
  opacity: 1;
  visibility: visible;
}

.modal_md .modal__container {
  max-width: 600px;
}

.modal__container {
  position: absolute;
  top: 50%;
  left: 50%;
  width: calc(100% - 40px);
  max-width: 980px;
  transform: translate3d(-50%, -50%, 0);
}

[data-modal-target],
[data-modal-close] {
  cursor: pointer;
}

.modal-content {
  color: #333;
  background: #fff;
  box-shadow: 0 0 7px 2px rgba(0, 0, 0, 0.4);
  font-size: 14px;
  font-weight: 400;
  line-height: 1.4;
}

.modal-content__header {
  position: relative;
  padding: 15px 20px;
  color: #fff;
  background: #2e99db;
}

.modal-content__close {
  position: absolute;
  top: 0;
  right: 0;
  width: 60px;
  height: 60px;
  background: none;
  text-align: center;
  font-size: 0;
  transition: background 0.2s, box-shadow 0.2s;
}

.modal-content__close:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

.modal-content__close:hover {
  background: #1f7db7;
}

.modal-content__close:active {
  background: #1f7db7;
  box-shadow: inset 2px 2px 6px rgba(0, 0, 0, 0.2);
}

.modal-content__close ~ .modal-content__title {
  padding-right: 60px;
}

.modal-content__close .icon {
  display: inline-block;
  vertical-align: middle;
  fill: #fff;
  font-size: 20px;
}

.modal-content__title {
  font-size: 24px;
  font-weight: 500;
  line-height: 30px;
  text-align: left;
}

.modal-content__footer {
  padding: 20px;
  border-top: 1px solid rgba(51, 51, 51, 0.2);
  font-size: 0;
  text-align: right;
}

.modal-content__footer .btn + .btn {
  margin-left: 20px;
}

.modal-content__body {
  overflow: auto;
  max-height: calc(100vh - 201px);
}

.modal-content__body-in {
  padding: 40px 20px;
}

.modal-content .form-group__label {
  font-size: 18px;
}

.modal-content__subtitle {
  margin-bottom: 10px;
  font-size: 22px;
  line-height: 1.2;
}

.modal-content__date-form {
  margin-bottom: 20px;
}

.modal-content__table {
  overflow: auto;
}
